    addItem(link, linkDesc, linkTarget) {
      // console.log('addItem called for', linkDesc);
      // put in database first to get key
      // then use that key to add to menuItems
      this.createLinkAndPushToArray(link, linkDesc, linkTarget);
      this.addMode = false;
