    updateLink(key) {
      const element = this.menuItems.find((item) => item.key === key);
      console.log('updateLink called for key:', key, element.linkDesc);
      fetch(
        `https://vue-http-demo-3ffcc-default-rtdb.firebaseio.com/${this.dbName}/${key}.json`,
        {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            link: element.link,
            linkDesc: element.linkDesc,
            target: element.target,
          }),
        }
      )
        .then((response) => {
          if (response.ok) {
            return response.json();
          }
        })
        .then((data) => {
          this.isLoading = false;
          const results = [];
          for (const key in data) {
            results.push({
              id: key,
              link: data[key].link,
              linkDesc: data[key].linkDesc,
              target: data[key].target,
            });
          }
          this.results = results;
          console.log(this.results.length);
          console.log(this.results);
        })
        .catch((error) => {
          this.isLoading = false;
          this.error = 'Failed to load experiences';
          console.error('Error loading experiences:', error);
        });
    },

