                key: key,
                link: data[key].link,
                linkDesc: data[key].linkDesc,
                target: data[key].target,
              });
            }
          }
          this.results = results;
          // console.log('this.results.length:', this.results.length);
          // clear out the current array since this is used in all modes
          while (this.menuItems.length > 0) {
            this.menuItems.pop();
          }
          results.forEach((item) => {
            // console.log(item.link, item.linkDesc, item.target);
            this.menuItems.push({
              key: item.key,
              link: item.link,
              linkDesc: item.linkDesc,
              target: item.target,
            });
          });
          // sort menuItems by linkDesc
          this.menuItems.sort((a, b) => {
            const descA = a.linkDesc.toUpperCase();
            const descB = b.linkDesc.toUpperCase();
            if (descA < descB) {
              return -1;
            }
            if (descA > descB) {
              return 1;
            }
            return 0;
          });
