<template>
  <li @click="handleClick">
    <a v-if="subitems.length === 0" :href="link" :target="target">{{
      linkDesc
    }}</a>
    <span v-else @click="toggleClick"
      >{{ linkDesc }}
      <b v-if="!clicked">></b>
      <b v-else>v</b>
    </span>
    <div v-if="subitems.length > 0 && clicked">
      <link-item
        v-for="subitem in subitems"
        :key="subitem.id"
        :link="subitem.link"
        :linkDesc="subitem.linkDesc"
        :target="subitem.target"
      ></link-item>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    link: {
      type: String,
      required: true,
    },
    linkDesc: {
      type: String,
      required: true,
    },
    target: {
      type: String,
      required: false,
      default: '_self',
    },
    subitems: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      clicked: false,
    };
  },
  emits: ['close'],
  methods: {
    toggleClick() {
      this.clicked = !this.clicked;
    },
  },
};
</script>

<style scoped>
li {
  padding: 3px;
  margin: 5px;
  list-style-type: none;
}
</style>
