import { createApp } from 'vue';

import App from './App.vue';
import { createRouter, createWebHistory } from 'vue-router';
import BaseCard from './components/UI/BaseCard.vue';
import BaseButton from './components/UI/BaseButton.vue';
import BaseDialog from './components/UI/BaseDialog.vue';
import LinkItem from './components/UI/LinkItem.vue';
import LinkMenu from './components/UI/LinkMenu.vue';
// import MainMenu from './components/MainMenu.vue';
import TheNavigation from './components/TheNavigation/TheNavigation.vue';
import Print from './components/Printing/Print.vue';
import PrintMenu from './components/Printing/PrintMenu.vue';
import PrintLink from './components/Printing/PrintLink.vue';
// import MyStuff from './components/Printing/MyStuff.vue';
// import CBSStuff from './components/Printing/CBSStuff.vue';
import RVCheckLists from './components/RV/RVCheckLists.vue';
import RVPreHaulCheckList from './components/RV/RVPreHaulCheckList.vue';
import RVWinterizeCheckList from './components/RV/RVWinterizeCheckList.vue';
// import ProgRummyRules from './components/ProgRummyRules/ProgRummyRules.vue';
// import Test from './components/Printing/Test.vue';
const app = createApp(App);

app.component('base-card', BaseCard);
app.component('link-item', LinkItem);
app.component('link-menu', LinkMenu);
// app.component('main-menu', MainMenu);
app.component('base-button', BaseButton);
app.component('base-dialog', BaseDialog);
app.component('the-navigation', TheNavigation);
app.component('rv-check-lists', RVCheckLists);
// app.component('prog-rummy-rules', ProgRummyRules);
app.component('print', Print);
app.component('print-menu', PrintMenu);
app.component('print-link', PrintLink);
// app.component('cbs-stuff', CBSStuff);
// app.component('test', Test);

const router = createRouter({
  history: createWebHistory(),
  routes: [
    { path: '/', redirect: '/print' },
    {
      path: '/print',
      component: Print,
      children: [
        {
          path: '/print/menu/:mode',
          component: PrintMenu,
          props: true,
        },
      ],
    },
    // { path: '/prog-rummy-rules', component: ProgRummyRules },
    {
      path: '/rv',
      component: RVCheckLists,
      children: [
        { path: '/rv/PreHaul', component: RVPreHaulCheckList },
        { path: '/rv/Winterize', component: RVWinterizeCheckList },
      ],
    },
    { path: '/:notFound(.*)', redirect: '/print' },
  ],
  linkActiveClass: 'active',
});

app.use(router);

app.mount('#app');
